import React from "react";
import menuLogo from "~/assets/images/banner-logo.png";
import {
  DIETARY_PREFERENCES_ENG,
  DIETARY_PREFERENCES_DK,
  ALLERGENS_ENG,
  ALLERGENS_ENG_DK,
  EMPTY_ALLERGENS_ENG,
  EMPTY_ALLERGENS_DK,
  SECTION_1_ID
} from "~/utils/constants.js";
import { getAssetURL } from "~/utils/get-asset-url";

function LunchContent({
  copyText,
  language,
  isAdminUser,
  data,
  part,
  LocationId,
}) {
  const isMenuPresent = data
  ?.map((e, i) =>
    e?.recipe_id?.[
        language === "da" ? "menu_info" : "menu_info_1"
        ]?.trim()?.length > 0
      ? true
      : false
  )
  ?.includes(true)
  ? true
  : false;
  return (
    <div className="banner-main-bg lunch-content" key={Math.random()}>
      <div className="banner-bg text-white relative flex flex-col font-noto">
       {isMenuPresent &&  <div className="main-title flex justify-start w-full font-noto mb-4">
          <h1>Today's Menu</h1>
        </div> }
        {data?.map((e, i) => {
          const menu =
            language === "da"
              ? e?.recipe_id?.menu_info
              : e?.recipe_id?.menu_info_1;
          const stationImg = e?.station_name?.station_image_1?.id
          const titleCheck = e?.station_name?.station_name_translation?.filter(
            (e) => e?.location_id == LocationId
          );
          const StationName =
            titleCheck?.[0]?.name?.length > 0
              ? titleCheck?.[0]?.name
              : e?.station_name?.station_name;
          const isGoGreen = ( Number(e?.station_name?.id) === Number(SECTION_1_ID))  ? true : false

          return (
            <React.Fragment key={i}>
              {menu?.trim()?.length > 0 && (
                <div
                  key={i}
                  className="banner-contents flex flex-col items-center w-full py-5 "
                >
                  <div className="menu-contents flex text-start	w-full">
                    <div className="w-full items-center text-left text-section">
                      {menu?.trim()?.length > 0 && (
                        stationImg ?
                          <div className="station-image">
                              <img
                                src={getAssetURL(stationImg)}
                                alt="station image"
                                className={`pb-3 ${isGoGreen?'max-w-[80px]':'max-w-[120px]'}`}
                              />
                          </div>
                          :
                        <div className="station-name w-full font-medium text-start mb-2 text-lg uppercase">
                          {StationName}
                        </div>
                      )}
                      <div className="w-full text-md md:text-2xl pb-2 leading-none menu-title">
                        {menu}
                      </div>
                      {menu?.trim()?.length > 0 && (
                        <div className="w-full allergen md:text-lg">
                          <span className=" font-bold">
                            {language === "da"
                              ? ALLERGENS_ENG_DK
                              : ALLERGENS_ENG}
                            :{" "}
                          </span>
                          {e?.recipe_id?.allergens_list?.length > 0
                            ? e?.recipe_id?.allergens_list?.map(
                                (val, i) =>
                                  `${
                                    val?.Allergens_id?.[
                                      language === "da"
                                        ? "name"
                                        : "translated_name"
                                    ]
                                  }${
                                    i !==
                                    e?.recipe_id?.allergens_list?.length - 1
                                      ? ", "
                                      : ""
                                  }`
                              )
                            : language === "da"
                            ? EMPTY_ALLERGENS_DK
                            : EMPTY_ALLERGENS_ENG}
                        </div>
                      )}
                      {menu?.trim()?.length > 0 &&
                        e?.recipe_id?.dietary_list?.length > 0 && (
                          <div className="banner-dietary md:text-lg">
                            <span className=" font-bold">
                              {language === "da"
                                ? DIETARY_PREFERENCES_DK
                                : DIETARY_PREFERENCES_ENG}
                              :{" "}
                            </span>
                            {e?.recipe_id?.dietary_list?.map(
                              (val, i) =>
                                `${
                                  val?.Dietary_id?.[
                                    language === "da"
                                      ? "name"
                                      : "translated_name"
                                  ]
                                }${
                                  i !== e?.recipe_id?.dietary_list?.length - 1
                                    ? ", "
                                    : ""
                                }`
                            )}
                          </div>
                        )}
                    </div>
                    {menu?.trim()?.length > 0 && (
                      <div className="grid w-full gap-y-2 text-center co2-section">
                        {e?.recipe_id?.total_co2 != null &&
                          Number(e?.recipe_id?.total_co2)?.toFixed(2) > 0 && (
                            <div className="flex items-center">
                              <div className="circle h-[50px] w-[50px] md:h-[60px] md:w-[60px] rounded-full bg-[#485648] mr-2 md:mr-4 items-center justify-center flex flex-col">
                                <span className="font-semibold text-white text-[12px] value">
                                  {Number(e?.recipe_id?.total_co2)?.toFixed(2)}
                                </span>
                                <span className="text-[8px] md:text-[0.7rem] font-medium unit">
                                  kg
                                </span>
                              </div>
                              <span className="text-[10px] md:text-[0.7rem] md:leading-[0.8rem] font-medium co2-text uppercase">
                                co2
                              </span>
                            </div>
                          )}
                        {e?.recipe_id?.total_calories != null &&
                          Number(e?.recipe_id?.total_calories)?.toFixed(2) >
                            0 && (
                            <div className="flex items-center">
                              <div className="circle h-[50px] w-[50px] md:h-[60px] md:w-[60px] rounded-full bg-[#485648] mr-2 md:mr-4 items-center justify-center flex flex-col">
                                <span className="font-semibold text-white text-[12px] value">
                                  {Number(
                                    e?.recipe_id?.total_calories
                                  )?.toFixed(2)}
                                </span>
                                <span className="text-[8px] md:text-[0.7rem] font-medium unit">
                                  kcal
                                </span>
                              </div>
                              <span className="text-[10px] md:text-[0.7rem] md:leading-[0.8rem] font-medium co2-text">
                                {language === "da" ? "Kalorier" : "Calories"}
                              </span>
                            </div>
                          )}
                      </div>
                    )}
                  </div>
                </div>
              )}
            </React.Fragment>
          );
        })}
         {isMenuPresent && (
          <div className="info-text border-t-2 block w-full italic text-white pt-4 text-left">
            <span className="font-bold">
              {language === "da"
                ? "Information om allegene ingredienser "
                : "Food allergies and intolerances - "}
            </span>
            {language === "da"
              ? "kan fås ved henvendelse til vores kokkenpersonale."
              : "please speak to our staff about your requirement"}
          </div>
        )}
      </div>
      {isAdminUser && (
        <div className="absolute bottom-5 left-5 text-white bg-black p-2">
          <button onClick={() => copyText({ language: language, type: part })}>
            Copy Link <i className="ri-clipboard-line"></i>
          </button>
        </div>
      )}
    </div>
  );
}

export default LunchContent;
